<template>
	<div class="minewrap">
		<div class="ding">
			<img src="../assets/images/ic_user_male.png" class="toux">
			<span class="kuang">登陆 / 注册</span>
		</div>
		<div class="tixing">
			<div class="xiaohezi">
				<img src="../assets/images/ic_mine_notification.png" class="naozhong">提醒
				<span style="padding-left:250px;color:#999"> > </span>
			</div>
			<div class="xiaohezi1">
				<p style="color:#999;font-size:12px;">暂无新提醒</p>
			</div>
		</div>
		<div class="icon" v-for='item in arr'>
			<div class="icon1">
				<span  class="imgg">
					<img :src="item.url1" class="imm">
					<p>{{ item.txt1 }}</p>
				</span>
				<span class="imgg">
					<img :src="item.url2" class="imm">
					<p>{{ item.txt2 }}</p>
				</span>
				<span class="imgg">
					<img :src="item.url3" class="imm">
					<p>{{ item.txt3 }}</p>
				</span>
				<span  class="imgg">
					<img :src="item.url4" class="imm">
					<p>{{ item.txt4 }}</p>
				</span>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		data:function(){
			return{
				arr:[
					{
						url1:require('../assets/images/ic_album_photo_liked.png'),
						url2:require('../assets/images/ic_my_note.png'),
						url3:require('../assets/images/ic_my_album.png'),
						url4:require('../assets/images/ic_my_status.png'),
						txt1:'喜欢',
						txt2:'日记',
						txt3:'相册',
						txt4:'我的广播'
					},
					{
						url1:require('../assets/images/ic_my_movies_tvs.png'),
						url2:require('../assets/images/ic_my_books.png'),
						url3:require('../assets/images/ic_my_music.png'),
						url4:require('../assets/images/ic_my_events.png'),
						txt1:'电影·电视',
						txt2:'读书',
						txt3:'音乐',
						txt4:'同城活动'
					},
					{
						url1:require('../assets/images/short_cut_for_group.png'),
						url2:require('../assets/images/ic_my_doulist.png'),
						url3:require('../assets/images/ic_my_orders.png'),
						url4:require('../assets/images/ic_my_wallet.png'),
						txt1:'豆瓣时间',
						txt2:'豆列',
						txt3:'订单',
						txt4:'钱包'
					}
				]
			}
		}
	}
</script>
<style type="text/css">
.imm{width: 30%;text-align: center;margin-bottom: 10px;}
.imgg{width: 24%;margin-top: 10px;height: 80px;display: inline-block;text-align: center;}
.icon{width: 100%;background:#fff;}
.icon1{border-bottom: 1px solid #eee;font-size: 12px;}
.icon2{border-bottom: 1px solid #eee;font-size: 12px;}
	.naozhong{width:25px;position: absolute;top:13px;left:0px;}
	.tixing{width: 100%;background:#fff;height: 100px;margin-top: 8px;margin-bottom: 8px;}
	.xiaohezi{width:84%;height:50px;border-bottom: 1px solid #eee;margin-left: 30px;line-height: 50px;font-size: 14px;position: relative;padding-left: 30px;}
	body{padding-top: 48px;}
	.xiaohezi1{text-align: center;line-height: 50px;}
	.toux{width:55px;margin-left: 20px;}
	.ding{width: 100%; background:url(../assets/images/123.png) no-repeat;padding:20px 0;background-size: 100%;position: relative;}
	.kuang{padding:8px 40px;border: 1px solid #fff;font-size: 12px;color:#fff;margin-left: 30%;position: absolute;top:30%;}
</style>


